.background-profile {
  background-image: url('../images-iclaim/blur-hospital.jpg');
}
/*แยก Style จาก*/
.containerStype{
  /*background-image: url('../images-iclaim/blur-hospital.jpg');*/
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh; /* ขนาดความสูงของพื้นหลังเท่ากับ viewport ให้ครอบทุกส่วนของเนื้อหา */
  width: 100%; /* พื้นหลังขนาดเท่ากับ containerStype */
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
.appBarStyle{
  position:static;
  width: 100%;
  padding: 15px;
}
.cardStyle {
  width: 90%;
  height: 60%;
  overflow-y: auto;
}
.BoxStyle {
  position: absolute;
  left: 0;
}
.logoStyle{
  height: 80px;
  margin-right: 10px;
  padding: 10px;
}
.Box1{
  position: absolute;
  right: 0;
}

.Box2{
  position: relative;
  display: flex;
  align-items: center;
  background-color: rgb(217,217,217);
  border: 1px rgb(217,217,217);
  border-radius: 6px;
  padding: 8px;
  max-width: auto;
  max-height: 20px;
}
.Avatar-img{
  margin-right: 9px;
  max-width: 28px;
  max-height: 28px;
}
.Menu-list{
    margin-top: 40px;
    padding: 0;
}
/* End */

.container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 20px;
  width: 90%;
  height: 60%;
}

.container-approve-reject {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  width: 80%;
  height: 60%;
}

button {
  border: none;
  background: none;
}

.homeicon {
  background: none;
  border: none;
  padding: 0;
  max-width: 50%;
  cursor: pointer;
}

.homeicon:hover {
  transform: scale(1.22);
}

.Fixlocation {
  position: static;
  left: 10%;
  top: 10%;
  margin-right: 15px;
  border: none;
  display: flex;
}
.Fixlocation-approve-reject {
  position: static;
  left: 10%;
  top: 10%;
  margin-right: 10px;
  border: none;
  display: flex;
}

.Dashboard-Internal-button {
  margin: 5px 0px 2px;
  max-width: 320px;
  display: flex;
  padding: 6px 25px;
  font-size: 16px;
  line-height: 1.25rem;
  font-weight: 700;
  text-align: center;
  vertical-align: middle;
  align-items: center;
  border-radius: 0.5rem;
  border: 1px solid rgba(217, 217, 217, 0);
  gap: 0.75rem;
  color: rgb(0, 0, 0);
  background-color: #c7c7c7;
  cursor: pointer;
  transition: all 0.0s ease;
  font-family: 'Kanit', sans-serif;
}
.modalStyle {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 70%; /* กำหนดความกว้างเป็น 70% */
  height: 90%; /* กำหนดความยาวเป็น 70% */
  max-width: 880px; /* กำหนดความกว้างสูงสุด */
  max-height: auto; /* กำหนดความยาวสูงสุด */
  background-color: #ffffff; /* กำหนดสีพื้นหลัง */
  border-radius: 8px;
  overflow: auto; /* เพิ่ม scrollbar ในกรณีที่ข้อมูลยาวเกินกำหนด */
  display: flex; /* ใช้ flexbox layout */
  flex-direction: column; /* จัดเรียงส่วนในแนวดิ่ง */
  align-items: center;
  justify-content: center;
}

.Dashboard-Internal-button:hover {
  transform: scale(1.10);
}
.Dashboard-Internal-button-date {
  margin: 5px 0px 2px;
  max-width: 100px;
  display: flex;
  padding: 6px 20px;
  font-size: 16px;
  line-height: 1.25rem;
  font-weight: 700;
  text-align: center;
  vertical-align: middle;
  align-items: center;
  border-radius: 0.5rem;
  border: 1px solid rgba(217, 217, 217, 0);
  gap: 0.75rem;
  color: rgb(0, 0, 0);
  background-color: #c7c7c7;
  cursor: pointer;
  transition: all 0.0s ease;
  font-family: 'Kanit', sans-serif;
}

.Dashboard-Internal-button-date:hover {
  transform: scale(1.10);
}
.custom-checkbox {
  background-color: rgb(255, 255, 255);
  border-radius: 50%;
  align-items: center;
}
.custom-checkbox1 {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 5px 50px;
  width: 90%;
  height: 60%;
}

.button-Approve {
  max-width: 320px;
  display: flex;
  padding: 0.6rem 1.4rem;
  font-size: 1.375rem;
  line-height: 1.25rem;
  font-weight: 700;
  text-align: center;
  vertical-align: middle;
  align-items: center;
  border-radius: 0.5rem;
  gap: 0.75rem;
  color: rgb(255, 255, 255);
  background-color: #1095c6;
  cursor: pointer;
  transition: all 0.0s ease;
  font-family: 'Kanit', sans-serif;
  padding: 10px 80px 10px 80px;
  margin: 0px 50px 0px 50px;
}

.button-Reject {
  max-width: 320px;
  display: flex;
  padding: 0.6rem 1.4rem;
  font-size: 1.375rem;
  line-height: 1.25rem;
  font-weight: 700;
  text-align: center;
  vertical-align: middle;
  align-items: center;
  border-radius: 0.5rem;
  gap: 0.75rem;
  color: rgb(255, 255, 255);
  background-color: #9c0606;
  cursor: pointer;
  transition: all 0.0s ease;
  font-family: 'Kanit', sans-serif;
  padding: 10px 80px 10px 80px;
  margin: 0px 50px 0px 50px;
}

.button-Approve:hover {
  transform: scale(1.02);
}

.button-Reject:hover {
  transform: scale(1.02);
}

.dark-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

.notification {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  width: 30%;
  height: 25%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.notification p {
  margin: 0;
  font-size: 24px;
  color: #000000;
  font-family: 'Kanit', sans-serif;
  font-weight: 700;
}
.imageInModal_Internal {
  margin: 20px;
  width: 820px; /* กำหนดความกว้างของรูปภาพ */
  height: auto; /* ให้ความสูงปรับตามอัตราส่วนของรูปภาพ */
}
.SuccessIcon-img {
  position: fixed;
  top: 20%;
  max-width: 50%;
  max-height: 50%;
}
.rejectReasonInput_Internal{
  margin-left: 30px;
  width: 800px;
  padding: 5px;
}
.modalStyle p {
  position: fixed;
  top: 25%;
  font-size: 30px;
  color: #000000;
  font-family: 'Kanit', sans-serif;
  font-weight: 700;
  text-align: center;
}

.FailIcon-img {
  position: fixed;
  max-width: 20%;
  top: 1%;
}

.input-container {
  position: relative;
  margin-bottom: 1rem;
}

.input-field {
  width: 100%;
  padding: 0.5rem;
  border: 2px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.input-field:focus {
  outline: none;
  border-color: #007bff;
}

.input-label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 10px;
  color: #555;
  font-size: 1rem;
  transition: top 0.3s ease, font-size 0.3s ease, color 0.3s ease;
}

.input-field:focus + .input-label,
.input-field:not(:placeholder-shown) + .input-label {
  top: 0;
  font-size: 0.75rem;
  color: #007bff;
}

@media (max-width: 768px) {
  .container,
  .container-approve-reject {
      width: 100%;
  }
  .button-Approve,
  .button-Reject {
      width: 100%;
      margin: 5px 0;
      padding: 10px 40px;
      font-size: 1rem;
  }
  .modalStyle {
      width: 80%;
  }
}

